.districtRow {
  width: 2322px;
  height: 542px;
  border-style: solid;
  border-color: transparent;
  border-width: 400px 310px 60px 165px;
  border-image: url(/images/imgs/框.png);
  border-image-slice: 400 310 60 165 fill;
  padding: 0;
  margin-top: 29px;
}
.districtRow .sbox {
  height: 460px;
  margin-top: -400px;
  margin-left: -89px;
  margin-right: -432px;
  margin-bottom: -15px;
}
.districtRow .oneBox:nth-child(1) {
  border: 2px dashed #0090ff;
}
.districtRow .oneBox:nth-child(2) {
  margin: 50px 0 0 30px;
  border: 2px dashed #0090ff;
  border-style: dashed none dashed dashed;
}
.districtRow .oneBox:nth-child(3) {
  border: 2px dashed #0090ff;
  border-style: dashed none dashed none;
}
.districtRow .oneBox:nth-child(4) {
  border: 2px dashed #0090ff;
  border-style: dashed none dashed none;
}
.districtRow .oneBox:nth-child(5) {
  border: 2px dashed #0090ff;
  border-style: dashed dashed dashed none;
}
.districtRow .oneBox {
  display: inline-block;
  position: relative;
  width: 18%;
  height: 460px;
}
.districtRow .oneBox .codCircle {
  position: absolute;
  top: 88px;
  left: 42px;
}
.districtRow .oneBox p {
  letter-spacing: 2px;
  position: absolute;
  font-size: 33px;
  color: #ffffff;
}
.districtRow .oneBox span {
  position: absolute;
  left: -2%;
  top: 40%;
  font-size: 40px;
  color: #0090ff;
  letter-spacing: 2px;
}
.spanA {
  position: absolute;
  top: 90% !important;
  left: 35% !important;
  margin-top: -13px;
}
.spanANew {
  position: absolute;
  top: 90% !important;
  left: 30% !important;
  margin-top: -13px;
}
.spanAA {
  position: absolute;
  top: 50% !important;
  left: 35% !important;
}
.spanAAA {
  position: absolute;
